<template>
  <div class="container py-5">
    <form class="w-100" v-on:submit.prevent="handleSubmit">
      <div class="text-center mb-4">
        <h1 class="h3 mb-3 font-weight-normal">Sign In</h1>
      </div>

      <div class="form-label-group mb-2">
        <label for="email">email</label>
        <input
          id="email"
          v-model="email"
          name="email"
          type="email"
          class="form-control"
          placeholder="email"
          autocomplete="username"
          required
          autofocus
        />
      </div>

      <div class="form-label-group mb-3">
        <label for="password">Password</label>
        <input
          id="password"
          v-model="password"
          name="password"
          type="password"
          class="form-control"
          placeholder="Password"
          autocomplete="current-password"
          required
        />
      </div>

      <button
        class="btn btn-lg btn-primary btn-block mb-3"
        type="submit"
        :disabled="isProcessing"
      >
        {{ isProcessing ? "" : "" }}
      </button>

      <div class="text-center mb-3">
        <p>
          <router-link to="/signup"> Sign Up </router-link>
        </p>
      </div>

      <p class="mt-5 mb-3 text-muted text-center">&copy; 2017-2018</p>
    </form>
  </div>
</template>

<script>
import authorizationAPI from "../apis/authorization.js";
import { Toast } from "../utility/helpers.js";

export default {
  //data
  data() {
    return {
      email: "",
      password: "",
      isProcessing: false,
    };
  },
  //methods
  methods: {
    //Submit(form)
    async handleSubmit(e) {
      try {
        // e.preventDefault();
        //JSON
        // console.log(
        //   JSON.stringify({
        //     email: this.email,
        //     password: this.password,
        //   })
        // );

        // TODO: 
        //  axios  Promise ,thenresponseresponse console
        if (!this.email || !this.password) {
          Toast.fire({
            icon: "warning",
            title: " email  password",
          });
          return;
        }
        //
        this.isProcessing = true;

        //     authorizationAPI
        //       .signIn({ email: this.email, password: this.password })
        //       .then((response) => {
        //         console.log(response);
        //         const { data } = response;

        //         if (data.status !== "success") {
        //           throw new Error(data.message);
        //         }

        //         localStorage.setItem("token", data.token);

        //         this.$router.push("/restaurants");
        //       })
        //       .catch((error) => {
        //         // 
        //         this.password = "";
        //         // 
        //         Toast.fire({
        //           icon: "warning",
        //           title: "",
        //         });
        //         this.isProcessing = false;
        //         console.log("error", error);
        //       });
        //   },
        // },

        const response = await authorizationAPI.signIn({
          email: this.email,
          password: this.password,
        });

        console.log(response);
        const { data } = response;

        if (data.status !== "success") {
          throw new Error(data.message);
        }

        localStorage.setItem("token", data.token);

        //setCurrentUserVuexstateVuexmutationsthis.$store.commitstoremutationsmethodsmutationsmethodmethoddata.usersetCurrentUsercurrentUser
        this.$store.commit("setCurrentUser", data.user);

        this.$router.push("/restaurants");
      } catch (error) {
        // 
        this.password = "";
        this.isProcessing = false;
        // 
        Toast.fire({
          icon: "warning",
          title: "",
        });
        console.log("error", error);
      }
    },

    //    methods: {
    //     handleSubmit (e) {
    //       const data = JSON.stringify({
    //         email: this.email,
    //         password: this.password
    //       })

    //       /
    //       console.log('data', data)
    //     }
    //   }
    // }
  },
};
</script>