import axios from 'axios'
import Swal from 'sweetalert2'
const baseURL = 'https://forum-express-api.herokuapp.com/api'

//axios.create()  axios  apiHelper apiHelper.get('/restaurants')

const axiosInstance = axios.create({
  baseURL: baseURL
  //baseURL = 'https://forum-express-api.herokuapp.com/api'

})

//apistokentokeninterceptors()token:
// const getToken = () => localStorage.getItem('token')
// create({ data1 }) {
//   return apiHelper.post('/comments', data1, { headers: { Authorization: `Bearer ${getToken()}` } })

// },


//
axiosInstance.interceptors.request.use(
  config => {
    const token = localStorage.getItem('token')

    if (token) {
      config.headers['Authorization'] = `Bearer ${token}`
    }
    return config
  },
  err => Promise.reject(err)
)

export const apiHelper = axiosInstance



