<template>
  <div class="container py-5">
    <NavTabs />
    <Spinner v-if="isLoading" />

    <template v-else>
      <!--  RestaurantsNavpills -->
      <RestaurantsNavPills :categories="categories" />
      <div class="row">
        <!--  RestaurantCard -->
        <RestaurantCard
          v-for="restaurant in restaurants"
          :key="restaurant.id"
          :initialRestaurant="restaurant"
        />
      </div>

      <!--  RestaurantPagination -->
      <RestaurantsPagination
        v-if="totalPage.length > 1"
        :currentPage="currentPage"
        :totalPage="totalPage"
        :previousPage="previousPage"
        :categoryId="categoryId"
        :nextPage="nextPage"
      />

      <div v-if="restaurants.length < 1"></div>
    </template>
  </div>
</template> 

<script>
import NavTabs from "../components/NavTabs.vue";
import RestaurantCard from "../components/RestaurantCard.vue";
import RestaurantsNavPills from "../components/RestaurantsNavPills.vue";
import RestaurantsPagination from "../components/RestaurantsPagination.vue";
import restaurantsAPI from "../apis/restaurants.js";
import { Toast } from "../utility/helpers.js";
import Spinner from "../components/Spinner";

export default {
  name: "Restaurants",
  components: {
    NavTabs: NavTabs,
    RestaurantCard,
    RestaurantsNavPills,
    RestaurantsPagination,
    Spinner,

    //templateNavTabs
  },
  data() {
    return {
      restaurants: [],
      categories: [],
      categoryId: -1,
      currentPage: 1,
      totalPage: [],
      previousPage: -1,
      nextPage: -1,
      isLoading: true,
    };
  },

  created() {
    //this.$route.queryquery string(?)
    console.log(this.$route.query);
    const { categoryId = "", page = "" } = this.$route.query;
    this.fetchRestaurants({ queryPage: page, queryCategoryId: categoryId });
  },

  //  beforeRouteUpdate 
  beforeRouteUpdate(to, from, next) {
    //categoryId = "", page = ""categoryIdpagecategoryIdpageundefined
    const { categoryId = "", page = "" } = to.query;
    this.fetchRestaurants({ queryPage: page, queryCategoryId: categoryId });
    next();
  },

  methods: {
    async fetchRestaurants({ queryPage, queryCategoryId }) {
      try {
        this.isLoading = true;
        const response = await restaurantsAPI.getRestaurants({
          page: queryPage,
          categoryId: queryCategoryId,
        });
        if (response.statusText !== "OK") {
          throw new Error(response.statusText);
        }

        // console.log(response);
        const {
          restaurants,
          categories,
          categoryId,
          page,
          totalPage,
          prev,
          next,
        } = response.data;

        this.restaurants = restaurants;
        this.categories = categories;
        this.categoryId = categoryId;
        this.currentPage = page;
        this.totalPage = totalPage;
        this.previousPage = prev;
        this.nextPage = next;
        this.isLoading = false;
      } catch (error) {
        this.isLoading = false;
        console.log("error", error);
        Toast.fire({
          icon: "error",
          title: "",
        });
      }
    },
  },
};
</script>
